'use strict';
import {JsonPortMarshaler} from "./marshaler";
import {ILogProvider} from "../interfaces/iLogProvider";

export class OutputMarshaler extends JsonPortMarshaler {
    constructor(public logProvider: ILogProvider) {
        super();
    }
    public log(message: string): void {
        this.logProvider.log(message);
    }
}